@import url('https://fonts.googleapis.com/css2?family=Vollkorn:wght@700&display=swap');

*{
  margin:0;
  padding:0;
  box-sizing:border-box;
  scroll-behavior:smooth;
}

:root{
  --color-1:#545871;
  --color-2:#9597A6;
  --color-3:#DADBE6;
  --color-4:#F0F1F7;
  --color-5:#B8817D;
  --color-6:#EBD0CE;
  --color-7:#E6DBDA;
  --color-8:#F7ECEB;  
  --font--h1:48px;
  --font--h2:24px;
}

body{
  background: #eee;
}

/* Header */
header {
  height: 720px;
  background:url(Images/banner.png) no-repeat center;
  background-size: cover;
}


.picture{
  position:absolute;
  top:63px;
  gap:20px;
  left:128px;
  display:flex;
  align-items:center;
}

.cont-huella{
  display:flex;
  width:64px;
  height:64px;
  align-items: center;
  justify-content: center;
  background:#fff;
  border-radius: 50%;
}

.huella-img{
  height:38,47px;
  width:38,69px;
}

.picture-text{
  color:#fff;
  font-size:25px;
  font-family: 'Vollkorn', serif;
}

.Nav{
  position: absolute;
  right:104px;
  top:111px;
  display:flex;
  flex-direction: column;
  height:467px;
  width:417px;
}

ul{
  display:flex;
  gap:60px;
}

li{
  list-style:none;
  display:inline;
}

.Links{
  text-decoration:none;
  color:#fff;
}

.box-text{
  position:relative;
  top:38px;
  width:416px;
  height:323px;
}

.header-text{
  color:#fff;
  text-align:left;
  font-size:54px;
}

.header-btn{
  width:256px;
  height:56px;
  position:relative;
  color:#444;
  font-size:20px;
  top: 58px;
  border:transparent;
  border-radius:20px;
}

.img-text{
  position:absolute;
  color:#fff;
  left: 128px;
  bottom:100px;
}

/* About us */

.articulo{
  height:771px;
  display:flex;
  justify-content:space-around;
  align-items:center;
}

.articulo-aside{
  display:flex;
  flex-direction: column;
  justify-content:space-between;
  text-align:start;
  height:550px;
  width:480px;
}

.articulo-titulo{
  font-size:48px;
}

.articulo-subtitulo{
  font-size: 24px;
  font-weight:300;
}

.articulo-btn{
  width:256px;
  height:56px;
  background:#545871;
  color:#fff;
  font-size:24px;
  border:none;
  border-radius:30px;
}

.articulo-galeria {
  width: 574px;
  position: relative;
  display: flex;
  height: 550px;
  flex-wrap: wrap;
  gap: 34px;
}

.text-img1 {
  position: absolute;
  top: 205px;
  left: 30px;
  color: #fff;
}

.text-img2{
  position: absolute;
  top: 205px;
  right:230px;
  color: #fff;
}

.text-img3{
  position: absolute;
  top: 500px;
  left: 30px;
  color: #fff;
}

.text-img4{
  position: absolute;
  top:500px;
  right:220px;
  color: #fff;
}

/* Services */

.section{
  height:694px;
  background:url(./Images/client-pet.png) no-repeat #EBD0CE;
  display: flex;
  align-items:center;
}

.section-text{
  position:relative;
  top:250px;
  left:99px;
  color:#fff;
}

.section-article{
  width:415px;
  height:461px;
  position:absolute;
  display:flex;
  flex-direction: column;
  justify-content:space-between;
  right:129px;
}

.section-title{
  font-size:var(--font--h1);
}

.section-subtitle{
  font-size:var(--font--h2);
  font-weight:300;
}

.section-btn{
  width:256px;
  height:56px;
  border:none;
  border-radius:40px;
  color: #fff;;
  font-size:var(--font--h2);
  background:var(--color-1);
}

/* Reviews */

.section-review{
  height:974px;
  background:var(--color-1);
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.review-title{
  color:#fff;
  font-size:48px;
}

.reviews-cont {
  width: 1152px;
  height: 520px;
  display: flex;
  flex-wrap: wrap;
  gap:30px;
}

.box1 {
  height: 224px;
  width: 554px;
  display: flex;
  align-items: center;
  gap: 30px;
  padding: 30px;
  background: var(--color-8);
  border-radius: 20px;
}

.box-img {
  width: 160px;
  height: 160px;
}

.box-texts {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.more-reviews {
  width: 256px;
  height: 56px;
  border: none;
  border-radius: 40px;
  color:var(--color-1 );
  font-size:20px;
  background: var(--color-6);
}

/* Servicios-planes */

.servicios{
  height:1000px;
  display:flex;
  flex-direction:column;
  background:#e7e7e7;
  align-items: center;
  justify-content:space-evenly;
  }

.servicios-title{
  font-size:48px;
}

.servicios-subt{
  font-size:24;
  font-weight:300;
}

.servicios-subt span{
  display:block;
}

.cont-servicios{
  width:1248px;
  height:450px;
  display:flex;
  align-items:flex-end;
  justify-content: space-around;
}

.cont-icon{
  width:96px;
  height:96px;
  position:relative;
  bottom:50px;
  display:flex;
  align-items:center;
  justify-content:center;
  background:var(--color-8);
  border-radius: 50%;
}

.cont-services {
  width: 352px;
  height: 450px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border:1px solid #f5f5f5;
  border-radius:10px;
}

.cont-services h1{
  font-size:48px;
  color:var(--color-1);
}

.cont-services p{
  width:223px;
  height:124px;
  font: size 20px;
  text-align:center;
  font-weight:300;
}

.precios {
  width: 350px;
  height: 127px;
  background: var(--color-3);
  color:var(--color-1);
  display: flex;
  border-radius: 0px 0px 10px 10px;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

/* Footer */

.footer{
  height:504px;
  background:var(--color-8);
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color:var(--color-1);
  font-size:38px;
}

.footer h1{
  color: var(--color-2);
}

.footer h3{
  border-bottom:2px solid var(--color-1);
}